import React from "react";
import { useHistory } from "react-router-dom";
import { Card, Col, Row } from "antd";
import { Button, Form, Input } from "antd";
import { Link } from "react-router-dom";

export const Login = () => {
  const history = useHistory();
  const usuario = { login: "", password: "" };
  const ingresar = ({ login, password }) => {
    if (login === "") {
      alert("Ingrese su usuario");
      return;
    }
    if (password === "") {
      alert("Ingrese su contraseña");
      return;
    }
    if (login === "userdemo" && password === "userdemo1") {
      history.push("inicio");
    } else {
      alert("El usuario no existe o los datos son incorrectos.");
    }
  };

  return (
    <div className="login-body bdLogin">
      <div className="login-wrapper">
        <div className="login-panel">
          <Row>
            <Col span={24}>
              <Card
                title="INICIAR SESIÓN"
                style={{
                  background: "rgba(255, 255, 255, 0.85)",
                  padding: "10px 25px",
                }}
              >
                <Form
                  name="normal_login"
                  layout="vertical"
                  className="login-form"
                  initialValues={usuario}
                  onFinish={ingresar}
                >
                  <Form.Item
                    name="login"
                    label="Usuario"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese su usuario",
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    name="password"
                    label="Contraseña"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese su contraseña",
                      },
                    ]}
                  >
                    <Input type="password" />
                  </Form.Item>
                  <Form.Item>
                    <Link to="/forgotPassword">Olvide mi contraseña</Link>
                  </Form.Item>

                  <Form.Item>
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="login-form-button"
                      style={{ width: "100%" }}
                    >
                      Ingresar
                    </Button>
                  </Form.Item>
                </Form>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </div>
  );
};
