import React from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Space } from 'antd'

import HabilidadesService from './../service/HabilidadesService'
const habilidadesService = new HabilidadesService()

const Habilidad = () => {
    const history = useHistory()
    const { codigo } = useParams()
    const habilidades = habilidadesService.gets()
    const detallehabilidad = habilidadesService.get(codigo)
    const go = (url) => {
        history.push(`/${url}`);
    }
    const renderHabilidad = ({ id, nombre, imagen, url, video }) => <Col key={id} xs={24} sm={24} md={20} lg={20} xl={12} xxl={12} style={{ textAlign: 'center' }}>
        <Typography.Title level={4} style={{ color: '#000000', letterSpacing: '1px', textTransform: 'uppercase' }}>
            {nombre}
        </Typography.Title>
        {/* <Image alt={nombre} className='bdRoundBorder' src={`/assets/images/habilidades/${imagen}`} onClick={() => go(url)} preview={false} /> */}
        <div style={{ height: '350px' }}>
            {
                video
            }
        </div>
    </Col>

    if (detallehabilidad && detallehabilidad.id) {
        const { imagenTitulo } = habilidades
        const { titulo, botones, items } = detallehabilidad
        return (
            <>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                        <Row>
                        </Row>
                        <Row>
                            <Col span={24}>
                                <Image alt={titulo} src={`/assets/images/habilidades/${imagenTitulo}`} preview={false} />
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={18} lg={18} xl={16} xxl={16}>

                        <Row justify='center'>
                            <Col span={24} style={{ textAlign: 'center' }}>
                                <Typography.Title level={1} style={{ textTransform: 'uppercase' }} >
                                    {titulo}
                                </Typography.Title>
                            </Col>
                            {
                                items && items.length > 0 &&
                                <Col span={24}>
                                    <Row gutter={[15, 15]} justify='center'>
                                        {
                                            items.filter(f => f.visible).map(s => renderHabilidad(s))
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                    </Col>
                </Row>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                        {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                    </Col>
                </Row>
            </>
        )
    }
}

export default Habilidad