import React from "react";
import { useHistory } from "react-router-dom";
import { Image, Col, Layout, Row } from "antd";

import InicioService from "./../service/InicioService";
const inicioService = new InicioService();

const Dashboard = () => {
  const history = useHistory();
  const { items } = inicioService.get();
  const go = (url) => {
    history.push(`/${url}`);
  };
  const renderItem = ({ id, codigo, url, imagen, props }) => {
    return (
      <Col key={id} {...props}>
        <Image
          alt={codigo}
          src={imagen}
          className="bdRoundBorder bdPuntero bdShadow"
          onClick={() => go(url)}
          preview={false}
        />
      </Col>
    );
  };
  return (
    <Layout.Content>
      <div
        style={{
          color: "#333333",
          padding: "20px 0",
        }}
      >
        <Image
          alt="BYDZYNE"
          src="/assets/images/logoBlack.png"
          style={{ height: "80px", width: "80px" }}
        />
        <span
          style={{
            marginLeft: "20px",
            fontSize: "1.5em",
            letterSpacing: "3px",
            fontWeight: "bold",
          }}
        >
          BYDZYNE
        </span>
      </div>
      <Row justify="center" gutter={[15, 15]}>
        {items.map((hi) => renderItem(hi))}
      </Row>
    </Layout.Content>
  );
};

const comparisonFn = function (prevProps, nextProps) {
  return (
    prevProps.location.pathname === nextProps.location.pathname &&
    prevProps.colorMode === nextProps.colorMode
  );
};

export default React.memo(Dashboard, comparisonFn);
