import React from 'react'
import { useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Space } from 'antd'

import HabilidadesService from '../service/HabilidadesService'
const habilidadesService = new HabilidadesService()

const Habilidades = () => {
    const history = useHistory()
    const habilidades = habilidadesService.gets()
    const go = (url) => {
        history.push(`/${url}`);
    }
    const renderHabilidad = ({ id, titulo, nombre, imagen, url }) => <Col key={id} xs={24} sm={12} md={12} lg={12} xl={8} xxl={6} style={{ textAlign: 'center' }}>
        <Image alt={nombre} className='bdRoundBorder bdPuntero' src={`/assets/images/habilidades/${imagen}`} onClick={() => go(url)} preview={false} />
    </Col>

    if (habilidades && habilidades.id) {
        const { titulo, subTitulo, imagen, imagenTitulo, url, video, descripcion, botones, items } = habilidades
        return (
            <>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={16} lg={16} xl={16} xxl={16}>
                        <Row justify='center'>
                            <Col xs={24} sm={22} md={20} lg={28} xl={16} xxl={10}>
                                {
                                    imagenTitulo ?
                                        <Image alt={titulo} src={`/assets/images/habilidades/${imagenTitulo}`} preview={false} />
                                        :
                                        <>
                                            <Typography.Title level={1}>
                                                {titulo}
                                            </Typography.Title>
                                            <Typography.Title level={2}>
                                                {subTitulo}
                                            </Typography.Title>
                                        </>
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={18} lg={16} xl={16} xxl={14}>
                        <Row justify='center'>
                            <Col span={24} style={{ textAlign: 'center' }}>
                                <Typography.Title level={3}>
                                    {subTitulo}
                                </Typography.Title>
                                {/* <Image alt={subTitulo} className='bdRoundBorder' src={`/assets/images/habilidades/${imagen}`} preview={false} /> */}
                                <div style={{ height: '450px' }}>
                                    {
                                        video
                                    }
                                </div>
                            </Col>
                            <Col span={24} style={{ paddingTop: '20px', textAlign: 'center' }}>
                                <Typography.Title level={3}>
                                    {descripcion}
                                </Typography.Title>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={16} lg={16} xl={16} xxl={14}>
                        <Row justify='center'>
                            {
                                items && items.length > 0 &&
                                <Col span={24}>
                                    <Row gutter={[15, 15]} justify='center'>
                                        {
                                            items.map(s => renderHabilidad(s))
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                    </Col>
                </Row>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                        {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                    </Col>
                </Row >
            </>
        )
    }
}

export default Habilidades