import React from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Space } from 'antd'

import SMAService from '../service/SMAService'
const smaService = new SMAService()

const SMAServicio = () => {
    const history = useHistory()
    const { servicio } = useParams()
    const detalleServicio = smaService.get(servicio)
    const go = (url) => {
        history.push(`/${url}`);
    }
    const open = (url) => window.open(url)
    const renderServicio = ({ id, nombre, imagen, url, video, links }) => <Col key={id} xs={24} sm={20} md={18} lg={18} xl={12} xxl={12} style={{ textAlign: 'center' }}>
        {
            nombre &&
            <Typography.Title level={5} style={{ color: '#ffffff', letterSpacing: '1px', textTransform: 'uppercase' }}>
                {nombre}
            </Typography.Title>
        }
        {
            video ?
                <div style={{ height: '350px' }}>
                    {
                        video
                    }
                </div>
                : <Image alt={nombre} className='bdRoundBorder bdPuntero' src={`/assets/images/planAccion/servicios/${imagen}`} onClick={() => go(url)} preview={false} />
        }
        {
            links ?
                <>
                    <div className='bdNav'>
                        <ul>
                            {
                                links.map(({ id, nombre, url }) => <li key={id} onClick={() => open(url)}>
                                    {nombre}
                                    <span></span><span></span><span></span><span></span>
                                </li>)
                            }
                        </ul>
                    </div>
                </>
                : <></>
        }
    </Col>

    if (detalleServicio && detalleServicio.id) {
        const { titulo, subTitulo, botones, items } = detalleServicio
        return (
            <>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={16} lg={16} xl={16} xxl={16}>
                        <Row justify='center'>
                            <Col span={24} style={{ textAlign: 'center' }}>
                                <Typography.Title level={1} style={{ color: 'goldenrod' }}>
                                    {titulo}
                                </Typography.Title>
                                <Typography.Title level={2}>
                                    {subTitulo}
                                </Typography.Title>
                            </Col>
                            {
                                items && items.length > 0 &&
                                <Col span={24}>
                                    <Row gutter={[15, 15]} justify='center'>
                                        {
                                            items.map(s => renderServicio(s))
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                    </Col>
                </Row>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                        {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                    </Col>
                </Row>
            </>
        )
    }
}

export default SMAServicio