import React from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Space } from 'antd'

import TestimonioService from './../service/TestimonioService'
const testimonioService = new TestimonioService()

const Testimonio = () => {
    const history = useHistory()
    const { testimonio } = useParams()
    const testimonios = testimonioService.gets()
    const detalleTestimonio = testimonioService.get(testimonio)
    const go = (url) => {
        history.push(`/${url}`);
    }
    const renderTestimonio = ({ id, nombre, imagen, url, video }) => <Col key={id} xs={24} sm={22} md={20} lg={18} xl={12} xxl={12} style={{ textAlign: 'center' }}>
        <Typography.Title level={4} style={{ color: 'goldenrod', letterSpacing: '1px' }}>
            {nombre}
        </Typography.Title>
        {/* <Image alt={nombre} className='bdRoundBorder' src={`/assets/images/planAccion/testimonios/${imagen}`} onClick={() => go(url)} preview={false} /> */}
        <div style={{ height: '350px' }}>
            {
                video
            }
        </div>
    </Col>

    const renderTitulo = () => {
        const { codigo, titulo, imagenTitulo } = testimonios
        return (imagenTitulo ?
            <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
            :
            <>
                <Typography.Title level={1}>
                    {titulo}
                </Typography.Title>
            </>)
    }

    if (detalleTestimonio && detalleTestimonio.id) {
        const { subTitulo, botones, items } = detalleTestimonio
        return (
            <>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                        <Row>
                            <Col span={24}>
                                {
                                    renderTitulo()
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row justify='center' gutter={[20, 20]}>
                    <Col xs={22} sm={22} md={20} lg={18} xl={18} xxl={16}>
                        <Row justify='center'>
                            <Col span={14} style={{ textAlign: 'center' }}>
                                <Typography.Title level={2} style={{ color: 'goldenrod' }}>
                                    {subTitulo}
                                </Typography.Title>
                            </Col>
                            {
                                items && items.length > 0 &&
                                <Col span={24}>
                                    <Row gutter={[15, 15]} justify='center'>
                                        {
                                            items.map(s => renderTestimonio(s))
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                    </Col>
                </Row>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                        {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                    </Col>
                </Row>
            </>
        )
    }
}

export default Testimonio